body {
  font-family: sans-serif;
  margin: 50px;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#outer {
  height: 35px;
  width: 100px;
  border: 2px solid black;
  border-radius: 20px;
  margin: auto;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: all 0.5s;
}

#inner {
  height: 30px;
  width: 30px;
  background-color: black;
  border-radius: 20px;
  transition: transform 0.5s;
}
